.social {
  width: 200px;
  display: flex;
  flex-wrap: wrap;
}

.social__link {
  position: relative;
  width: 50px;
  height: 50px;
  margin-left: 10px;
  margin-right: 4px;

  border: 3px solid rgba(@bg-darkblue, 10%);
  border-radius: 50%;

  &--twitter {
    &::before {
      position: absolute;
      content: "";

      width: 18px;
      height: 15px;

      top: 16px;
      left: 15px;

      background-image: url("../img/icon-twitter.svg");
      background-repeat: no-repeat;
    }
  }

  &--facebook {
    &::before {
      position: absolute;
      content: "";

      width: 9px;
      height: 20px;

      top: 12px;
      left: 18px;

      background-image: url("../img/icon-facebook.svg");
      background-repeat: no-repeat;
    }
  }

  &--youtube {
    &::before {
      position: absolute;
      content: "";

      width: 15px;
      height: 15px;

      top: 16px;
      left: 17px;

      background-image: url("../img/icon-youtube.svg");
      background-repeat: no-repeat;
    }
  }

  &:hover,
  &:focus {
    border-color: @bg-darkblue;
    outline: none;
  }

  &:active {
    border-color: rgba(@bg-darkblue, 10%);
    outline: none;

    &::before {
      opacity: 0.3;
    }
  }
}

@media (min-width: @tablet-width) {
  .social {
    width: 208px;
  }

  .social__link {
    &--twitter {
      background-position: left 16px top 16px;
    }

    &--facebook {
      background-position: left 19px top 12px;
    }

    &--youtube {
      background-position: left 16px top 15px;
    }
  }
}
